import * as types from '../actions/ActionTypes';

// 초기 상태 정의
const initialState = {
  counters: [
    {
      color: 'black',
      number: 0
    }
  ]
}

// 리듀서 함수 정의
function counter(state = initialState, action) {
  // 레퍼런스 생성
  const { counters } = state;

  // 기존 배열에 직접 push()/pop() X
  // spread문법을 사용하거나 .slice()처럼 배열을 새로 생성 해야함
  switch(action.type) {
    // 카운터 새로 추가
    case types.CREATE:
      return {
        counters: [
          ...counters,
          {
            color: action.color,
            number: 0
          }
        ]
      };
    // slice를 이용해 맨 마지막 카운터를 제거
    case types.REMOVE:
      return {
        counters: counters.slice(0, counters.length - 1)
      };

    // action.index번째 카운터의 number에 1을 더함
    case types.INCREMENT:
      return {
        counters: [
          ...counters.slice(0, action.index),
          {
            ...counters[action.index],
            number: counters[action.index].number + 1
          },
          ...counters.slice(action.index + 1, counters.length)
        ]
      };

    // action.index번째 카운터의 number에 1을 뺌
    case types.DECREMENT:
    console.log(action.index);
      return {
        counters: [
          ...counters.slice(0, action.index),
          {
            ...counters[action.index],
            number: counters[action.index].number - 1
          },
          ...counters.slice(action.index + 1, counters.length)
        ]
      };

    // action.index번째 카운터의 색상을 변경
    case types.SET_COLOR:
      return {
        counters: [
          ...counters.slice(0, action.index),
          {
            ...counters[action.index],
            color: action.color
          },
          ...counters.slice(action.index + 1, counters.length)
        ]
      };

    default:
      return state;
  }
};

export default counter



